<template>
  <div>
    <div class="header">
      <el-menu
        class="el-menu-demo"
        mode="horizontal"
        :default-active="activeIndex"
        :router="true"
        background-color="#e7e7e7"
      >
        <el-menu-item
          :key="index"
          v-for="(item,index) in navList"
          @click="active(item.path)"
          :index="'/'+item.path"
        >{{item.name}}</el-menu-item>
      </el-menu>
      <!-- <el-menu
        :default-active="activeIndex"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
      >
        <el-menu-item index="1">特色爱心早餐</el-menu-item>
        <el-menu-item index="2">饮品中心</el-menu-item>
        <el-menu-item index="3">烧烤</el-menu-item>
        <el-menu-item index="4">火锅</el-menu-item>
        <el-menu-item index="5">中餐厅</el-menu-item>
        <el-menu-item index="6">北方面食</el-menu-item>
      </el-menu>-->
      <div class="right">
        <div class="right_one">
          <a href>
            <el-tooltip class="item" effect="dark" :content="'当前用户是：'+who" placement="bottom">
              <span>当前用户：{{who}}</span>
            </el-tooltip>
          </a>
        </div>
        <!-- <el-badge :value="12" class="item">
          <el-button size="small">
            <i class="el-icon-shopping-cart-2"></i>
          </el-button>
        </el-badge>-->
        <div class="right_two">
          <el-tooltip class="item" effect="dark" content="我的购物车" placement="bottom">
            <i class="el-icon-shopping-cart-2" @click="toMyCart"></i>
          </el-tooltip>
        </div>
        <div class="right_three">
          <el-button type="primary" @click="logOut">退出登录</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      who: "",
      activeIndex: "/gooddrink",
      navList: [
        { name: "首页", path: "gooddrink" },
        { name: "特色爱心早餐", path: "breakfast" },
        { name: "饮品中心", path: "drink" },
        { name: "烧烤", path: "roast" },
        { name: "西餐厅", path: "hotPot" },
        { name: "中餐厅", path: "chineseFood" },
        { name: "北方面食", path: "noodle" },
      ],
    };
  },
  methods: {
    logOut() {
      //清空token
      window.sessionStorage.clear();
      //跳转到登录页
      this.$router.push("/login");
    },
    active(changeActive) {
      this.activeIndex = "/" + changeActive;
      // console.log(chechangeActiveck);
    },
    toMyCart() {
      this.$router.push("/mycart");
    },
  },
  created() {
    const how = JSON.parse(window.sessionStorage.getItem("token"));
    // console.log(how.account);
    // const who = how[1];
    this.who = how.account;
    // console.log(JSON.parse(window.sessionStorage.getItem("token")));
  },
};
</script>
<style lang="stylus" scoped>
.right_one, .right_two, .right_three {
  float: left;
}

.right_one {
  line-height: 80px;
}

.right_two {
  // margin-left: 20px;
  line-height: 100px;
}

.item {
  margin-top: 10px;
  margin-right: 40px;
}

.el-menu {
  margin-left: 10%;
  float: left;
}

.el-menu-item {
  font-size: 20px;
}

.logo {
  margin-right: 5%;
  margin-top: 0.5%;
  float: left;
}

.header {
  height: 70px;
  background-color: #e7e7e7;
}

span {
  line-height: 70px;
  font-size: 15px;
}

.right_three {
  margin-top: 3%;
}

.el-button {
  margin-left: 120px;
  margin-top: 0.8%;
}

