<template>
  <div class="box">
    <div class="s">
      <div class="logo">
        <a href>
          <img src="../assets/img/logo3.png" alt />
        </a>
      </div>
      <div class="search">
        <el-input v-model="search" placeholder="请输入内容">
          <el-button type="danger" @click="getIndexMenu" slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </div>
      <!-- <div id="qrcode" class="qrcode" ref="qrCodeUrl"></div> -->
    </div>
    <!-- 抽奖 -->
    <div class="ward">
      <div class="t">每日一抽</div>
      <div class="choujiang">
        <div
          :key="index1"
          v-for="(item,index1) in prizeList1"
          class="line_item"
          :class="index == index1&&index!==4 ? 'active' : 'white_item'"
        >
          <div v-if="index1==4" @click="goLottery">{{item.prizename}}</div>
          <div v-else>{{item.prizename}}</div>
        </div>
        <el-dialog title="中奖情况" :visible.sync="dialogVisible" width="30%">
          <span>{{prizeList1[this.index].prizename}}</span>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
          </span>
        </el-dialog>
      </div>
    </div>
    <!-- 抽奖结束 -->
    <main-food></main-food>

    <div class="spase"></div>
    <h2>饮品补给站</h2>
    <el-divider>
      <i class="el-icon-cold-drink" style="font-size:30px;color:green"></i>
    </el-divider>
    <el-row class="row">
      <el-col :span="3" v-for="(item, index) in drinkList" :key="index">
        <el-card :body-style="{ padding: '0px' }">
          <div class="demo-image__preview">
            <el-tooltip class="item" effect="dark" :content="item.name" placement="top-start">
              <el-image :src="item.img" class="image" :preview-src-list="url" lazy></el-image>
            </el-tooltip>
          </div>
          <div style="padding: 14px;">
            <span>{{item.name}}</span>
            <br />
            <span style="color:red">{{'￥'+item.price}}</span>
            <el-rate
              v-model="item.dengji"
              disabled
              show-score
              text-color="#ff9900"
              score-template="{value}"
            ></el-rate>
            <div class="bottom clearfix">
              <div>
                <el-tooltip
                  class="item"
                  effect="dark"
                  :content="item.content"
                  placement="top-start"
                >
                  <span class="content">{{item.content}}</span>
                </el-tooltip>
              </div>
              <!-- <time class="time">{{ currentDate }}</time> -->
              <el-input-number v-model="item.num" :min="0" :max="10"></el-input-number>
              <el-button type="danger" class="button" @click="drinkBuy(item.id)">立即购买</el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <!-- <div class="row">
      <div class="drink">
        <img src="../assets/img/drink5.jpg" alt />
        <div>
          <span>ll</span>
        </div>
      </div>
    </div>-->
  </div>
</template>
<script>
import { mapMutations } from "vuex";
import QRCode from "qrcodejs2";
import MainFood from "../components/main/MainFood";
export default {
  data() {
    return {
      // 搜索
      search: "",
      // 抽奖
      dialogVisible: false,
      prizeList1: [],
      prizeList: [
        { prizename: "一元代金卷", free: 1 },
        { prizename: "五元代金券", free: 5 },
        { prizename: "两元代金券", free: 2 },
        { prizename: "谢谢惠顾", free: 0 },
        { prizename: "立即抽奖", free: 0 },
        { prizename: "一元代金券", free: 1 },
        { prizename: "10元代金券", free: 10 },
        { prizename: "5元代金券", free: 5 },
        { prizename: "再接再厉", free: 0 },
      ],
      index: 0, // 当前转动到哪个位置，起点位置
      count: 8, // 总共有多少个位置
      timer: 0, // 每次转动定时器
      speed: 50, // 初始转动速度
      times: 0, // 转动次数
      cycle: 30, // 转动基本次数：即至少需要转动多少次再进入抽奖环节
      prizes: 3, // 中奖位置
      prize: [0, 1, 2, 3, 5, 6, 7, 8], // 奖品位置
      //总价
      allPrice: 0,
      drinkList: [],

      fits: ["fill", "contain", "cover", "none", "scale-down"],
      url: [
        "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
      ],
    };
  },
  methods: {
    ...mapMutations(["addcart"]),
    /* createQrCode() {
      console.log("222");
      console.log(qrcode);
      var qrcode = new QRCode("qrcode", {
        text: "应昌宏的项目二维码", // 需要转换为二维码的内容
        width: 100,
        height: 100,
        colorDark: "#000000",
        colorLight: "#ffffff"
      });
      console.log("333");
      console.log(qrcode);
		},
		*/
    // -----------
    drinkBuy(id) {
      let choose = this.drinkList.filter((item) => {
        return item.id == id;
      });
      this.allPrice = choose[0].price * choose[0].num;
      // console.log(this.allPrice);
      // console.log(choose);
      this.$store.commit("addcart", choose);
      this.$message.success("添加宝贝成功！");
    },
    async getIndexMenu() {
      const res = await this.$http.get("indexmenu", {
        params: { search: this.search },
      });
      const res2 = await this.$http.get("prizelist");
      this.drinkList = res.data;
      this.prizeList1 = res2.data;
      console.log(this.prizeList1);
      // console.log(this.prizeList1[0].prizename);
    },
    goLottery() {
      //判断如果抽奖停止后才可以再次点击
      if (this.speed == 50) {
        this.prizes = this.prize[Math.floor(Math.random() * this.prize.length)];
        console.log(this.prizes);
        this.startRoll();
      }
    },
    // 开始转动
    startRoll() {
      this.times += 1; // 转动次数
      this.oneRoll(); // 转动过程调用的每一次转动方法，这里是第一次调用初始化
      this.usePrize();
    },
    // 每一次转动
    oneRoll() {
      let index = this.index; // 当前转动到哪个位置
      const count = 8; // 总共有多少个位置
      index += 1;
      if (index > count) {
        index = 0;
      }
      this.index = index;
    },
    usePrize() {
      // 如果当前转动次数达到要求 && 目前转到的位置是中奖位置
      if (this.times > this.cycle + 10 && this.prizes === this.index) {
        clearTimeout(this.timer); // 清除转动定时器，停止转动
        this.times = 0;
        this.speed = 50; //速度初始化
        // alert(this.prizeList[this.index].prizename);
        this.dialogVisible = true;
        const token = JSON.parse(window.sessionStorage.getItem("token"));
        // this.$message.success();
        this.$http.get("addprice", {
          params: {
            howmush: parseInt(this.prizeList1[this.index].free),
            account: token.account,
          },
        });
      } else {
        if (this.times < this.cycle) {
          this.speed += 10; // 降低转动速度
        }
        this.timer = setTimeout(this.startRoll, this.speed);
      }
    },
  },
  created() {
    this.getIndexMenu();
  },
  mounted() {},
  components: {
    "main-food": MainFood,
  },
};
</script>
<style lang="stylus" scoped>
