  height: 200px;
}

.drink {
  height: 350px;
  width: 15%;
  box-shadow: 0px 0px 5px 0px #9a9999;
  margin-left: 50px;
  margin-right: 50px;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  text-align: center;
  font-size: 10px;
  width: 80px;
  height: 40px;
  float: right;
}

.image {
  width: 80%;
  display: block;
  margin-left: 10%;
}

.clearfix:before, .clearfix:after {
  display: table;
  content: '';
}

