  clear: both;
}

.el-col.el-col-3 {
  margin-left: 20px;
  margin-bottom: 20px;
}

.el-col-3 {
  width: 18%;
}

.content {
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
