import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        mycart: '',
        totalprice: 0
    },
    mutations: {
        // state是必须的
        addcart(state, val) {
            this.totalprice = val[0].price * val[0].num
            val[0].totalPrice = this.totalprice
                // console.log(val);
            this.mycart = val[0];
            // console.log(this.mycart);
            let token = JSON.parse(window.sessionStorage.getItem('token'))
            token.arr.push(this.mycart)
                // console.log(total);
                // token.arr.push({ totalprice: total })
            console.log(token.arr);
            window.sessionStorage.setItem('token',
                    JSON.stringify(token))
                // console.log(window.sessionStorage.getItem('token'));
        },
    },
    actions: {
        pay(state, val) {
            const cookie = JSON.parse(window.sessionStorage.getItem('token'))
            axios.get('http://localhost:3002/pay/' + cookie.account, { params: { val: val } })
        }
    },
    modules: {}
})