<template>
  <div class="block">
    <el-carousel height="500px">
      <el-carousel-item v-for="(item,index) in imgList" :key="index">
        <img :src="item.url" alt />
      </el-carousel-item>
    </el-carousel>
    <!-- ------------------- -->
    <!-- <div class="amap-page-container">
      <div :style="{width:'100%',height:'500px'}">
        <el-amap vid="amap" disabled="false" :plugin="plugin" class="amap-demo" :center="center">
          <el-amap-marker vid="marker" :position="center" :label="label"></el-amap-marker>
          <el-amap-circle
            vid="circle"
            :center="center"
            :radius="radius"
            fill-opacity="0.2"
            strokeColor="#38f"
            strokeOpacity="0.8"
            strokeWeight="1"
            fillColor="#38f"
          ></el-amap-circle>
        </el-amap>
      </div>
    </div>-->
    <!-- ---------------------------- -->
  </div>
</template>

<script>
export default {
  data() {
    const self = this;
    return {
      // 地图内容
      // 地图提示文字
      label: {
        content: "钦汇园",
        offset: [10, 12]
      },
      // 增加地图区域框框
      radius: 100,
      center: [121.59996, 31.197646],
      lng: 0,
      lat: 0,
      loaded: false,
      plugin: [
        {
          size: 1300 * 1300,
          enableHighAccuracy: true, //是否使用高精度定位，默认:true
          timeout: 100, //超过10秒后停止定位，默认：无穷大
          maximumAge: 0, //定位结果缓存0毫秒，默认：0
          convert: true, //自动偏移坐标，偏移后的坐标为高德坐标，默认：true
          showButton: true, //显示定位按钮，默认：true
          buttonPosition: "RB", //定位按钮停靠位置，默认：'LB'，左下角
          showMarker: true, //定位成功后在定位到的位置显示点标记，默认：true
          showCircle: true, //定位成功后用圆圈表示定位精度范围，默认：true
          panToLocation: true, //定位成功后将定位到的位置作为地图中心点，默认：true
          zoomToAccuracy: true, //定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：f
          extensions: "all",
          pName: "Geolocation",
          events: {
            init(o) {
              // o 是高德地图定位插件实例
              o.getCurrentPosition((status, result) => {
                console.log(result);
                if (result && result.position) {
                  self.lng = result.position.lng;
                  self.lat = result.position.lat;
                  self.center = [self.lng, self.lat];
                  self.loaded = true;
                  self.$nextTick();
                }
              });
            }
          }
        }
      ],
      imgList: [
        { url: require("../../assets/img/drink5.jpg"), name: "李四" },
        { url: require("../../assets/img/good/a1.jpg"), name: "李四" }
      ]
    };
  },
  methods: {
    // 地图范围
    addRadius() {
      this.radius += 10;
    }
  }
};
</script>
<style lang="stylus" scoped>
.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}

.block {
  margin-top: 10px;
  width: 40%;
  height: 500px;
  margin-left: 30%;
}

img {
  width: 100%;
  height: 500px;
}

/deep/.el-carousel__button {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #4CAF50;
}
</style>