Vue.use(VueRouter)

const routes = [
    { path: '/', redirect: '/login' },
    { path: '/login', component: Login },
    {
        path: '/home',
        component: Home,
        redirect: '/gooddrink',
        children: [
            { path: '/breakfast', component: Breakfast },
            { path: '/gooddrink', component: GoodDrink },
            { path: '/drink', component: Drink },
            { path: '/roast', component: Roast },
            { path: '/hotPot', component: HotPot },
            { path: '/chineseFood', component: ChineseFood },
            { path: '/noodle', component: Noodle },

        ]
    },
    { path: '/mycart', component: MyCart }
]

const router = new VueRouter({
    routes
})
router.beforeEach((to, from, next) => {
    if (to.path == '/login') return next()
    const tokenS = window.sessionStorage.getItem('token')
    if (!tokenS) return next('/login')
    next()
})

export default router